<template lang="html">
  <section v-show="post" class="post-editor">
    <div class="post-editor__background" @click="closeEditor"></div>
    <div class="post-editor__content">
      <editor v-show="post">
      </editor>
    </div>
    <button class="post-editor__close" type="button" @click="closeEditor"></button>
  </section>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import Editor from './Editor'

export default {
  components: {
    Editor
  },
  computed: {
    ...mapState({
      post: state => state.editor.postEditing
    })
  },
  methods: {

    // return this.postId !== null ? 'is-active' : ''

    ...mapMutations({
      closeEditor: 'CLOSE_EDITOR'
    })
  }
}
</script>

<style lang="scss">
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/components/modal.sass";

  .post-editor{
    @extend .modal, .is-active;

    &__background{
      @extend .modal-background;
    }

    &__content {
      @extend .modal-content;
    }

    &__close {
      @extend .modal-close;
    }
  }
</style>



