<template lang="html">
  <form class="form">
    <div class="form__field">
      <label class="form__field__label" for="titleInput">Post Title:</label>
      <p class="form__field__control">
        <input class="form__field__input" name="titleInput" v-model="post.title" required>
      </p>
    </div>
    <div class="form__field">
      <label class="form__field__label" for="postBody">Post Body:</label>
      <p class="form__field__control">
        <textarea name="postBody" class="form__field__textarea" v-model="post.body" required></textarea>
      </p>
    </div>
    <div class="form__field--grouped">
      <p class="form__field__control">
        <button class="form__field__button" @click.prevent="closeEditor">Cancel</button>
      </p>
      <p class="form__field__control">
        <button :class="getActiveState()" type="submit" class="form__field__button--cta" @click.prevent="modifyPost(post)">Save</button>
      </p>
    </div>
  </form>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  computed: {
    ...mapState({
      post: state => state.editor.postEditing || {}
    })
  },
  methods: {
    ...mapActions([
      'modifyPost'
    ]),
    ...mapMutations({
      closeEditor: 'CLOSE_EDITOR'
    }),
    getActiveState () {
      return this.post.body === '' || this.post.title === '' ? 'is-disabled' : null
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/elements/form.sass";
  @import "./bulma/sass/elements/box.sass";
  @import "./bulma/sass/elements/button.sass";

  %padding-fix {
    box-sizing: border-box;
    height: 2.5em;
  }

  .form {
    @extend .box;

    &__field {
      @extend .field;

      &--grouped {
          @extend .field, .is-grouped;
      }

      &__control {
        @extend .control;

      }

      &__label {
          @extend .label;
      }

      &__input {
        @extend .input, %padding-fix;
      }

      &__textarea {
        @extend .textarea, %padding-fix;
        font-family: $family-sans-serif;
      }

      &__button {
        @extend .button;

        &--cta {
          @extend .button, .is-primary;
        }
      }
    }
  }

</style>



